.browsertab * {
    box-sizing: border-box;
  }
  
  .browsertab {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 19px;
    margin: 0;
    padding: 0;
  }
  
  .browsertab  .app {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #634cec;
    min-height: 100vh;
  }
  
  .browsertab  .browser {
    display: flex;
    flex-direction: column;
    background: #ffb91f;
    border-radius: 5px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
    min-width: 60%;
    min-height: 50vh;
  }
  
  .browsertab  .tabs {
    flex-shrink: 1;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    padding-top: 10px;
  }
  
  .browsertab  .tab {
    position: relative;
    background: #6e45df;
    min-width: 120px;
    max-width: 300px;
    font-size: 14px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    transition: 0.3s ease all;
    overflow: hidden;
    cursor: pointer;
  }
  
  .browsertab  .tab a {
    display: block;
    padding: 12px 25px;
    color: #e1daf5;
    text-decoration: none;
  }
  
  .browsertab .tab a.is-active {
    background: #5131a9;
    color: #eee;
  }
  
  .browsertab  .address-bar {
    background: #5533b3;
    padding: 8px;
  }
  
  .browsertab .address-bar input {
    background: #3a2081;
    color: #eee;
    border-radius: 20px;
    border: none;
    outline: none;
    width: 100%;
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .browsertab .viewport {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #5131a9;
    color: #dacff7;
    flex: 1;
    flex-grow: 1;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  
  /**
   * Add your highlight code here
   */
  
   .browsertab .highlight {
     display: block;
     position: absolute;
     height: 80px; 
     width: 300px;
     top: -10px;
     border-radius: 50%;
     transition: 0.3s ease all;
     opacity: 0.8;
     pointer-events: none;
     background: radial-gradient(
       rgba(157,128,238,1),
       rgba(157,128,238,0));  
   }